* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: 'Josefin Sans', sans-serif;
  margin: 0;
  padding: 0;
  background-image: url("./leafs.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 720px;
  height: 100vh;
}

.myClockPage{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  height:800px;
}

.myClock{
  width: 50%;
  height: auto;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  color:white;
}

h1{
  font-size: 50px;
}

h2{
  font-size: 40px;
}

button{
  font-size: 15px;
  font-family: 'Josefin Sans', sans-serif;
  padding: 15px;
  border-radius: 30px;
  border-style: none;
  outline: none;
  margin: 10px;
  background: white;
  cursor: pointer;
}

.actionButtons {
  padding: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

  @media screen and (max-width: 600px) {
    body{
      height: 600px;
    }
  .myClock {
    margin-bottom: 150px;
    height: 600px;
  }
  button {
    padding: 10px 25px;
  }
}



